import React, { useState, useEffect } from 'react';
import * as SunCalc from 'suncalc';
import './App.css';

function App() {
  const [location, setLocation] = useState(null);
  const [sunsetData, setSunsetData] = useState(null);
  const [deviceOrientation, setDeviceOrientation] = useState(0);
  const [error, setError] = useState(null);
  const [isAligned, setIsAligned] = useState(false);

  const ALIGNMENT_TOLERANCE = 10;

  // 1. Obter geolocalização
  useEffect(() => {
    const getLocation = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            setLocation({
              lat: position.coords.latitude,
              lng: position.coords.longitude
            });
          },
          (err) => setError("Ative a localização para usar o app")
        );
      } else {
        setError("Geolocalização não suportada");
      }
    };
    getLocation();
  }, []);

  // 2. Calcular direção do pôr do sol
  useEffect(() => {
    if (location) {
      const now = new Date();
      const times = SunCalc.getTimes(now, location.lat, location.lng);
      const sunsetPos = SunCalc.getPosition(times.sunset, location.lat, location.lng);
      const azimuth = (sunsetPos.azimuth * 180 / Math.PI + 180) % 360;

      setSunsetData({
        time: times.sunset.toLocaleTimeString(),
        azimuth
      });
    }
  }, [location]);

  // 3. Sensor de orientação com suavização
  useEffect(() => {
    let lastAlpha = 0;
    const handleOrientation = (e) => {
      if (e.alpha !== null) {
        const alpha = 360 - e.alpha;
        const smoothed = lastAlpha * 0.8 + alpha * 0.2;
        lastAlpha = smoothed;
        setDeviceOrientation(smoothed);
      }
    };

    window.addEventListener('deviceorientation', handleOrientation, true);
    return () => {
      window.removeEventListener('deviceorientation', handleOrientation);
    };
  }, []);

  // 4. Cálculo do ângulo relativo e checar alinhamento
  const calculateRelativeAngle = () => {
    if (!sunsetData) return 0;
    const angle = (sunsetData.azimuth - deviceOrientation + 360) % 360;
    const delta = Math.min(Math.abs(angle), 360 - Math.abs(angle));
    setIsAligned(delta < ALIGNMENT_TOLERANCE);
    return angle;
  };

  const relativeAngle = calculateRelativeAngle();

  return (
    <div className="App">
      <header className="App-header">
        <h1>Bússola do Pôr do Sol</h1>

        {error && <p className="error">{error}</p>}

        {sunsetData && (
          <div className={`compass-container ${isAligned ? 'aligned' : ''}`}>
            <div className="compass">
              <div 
                className="arrow" 
                style={{ transform: `rotate(${relativeAngle}deg)` }}
              >
                <div className="sun-icon">☀️</div>
              </div>
              <div className="compass-circle"></div>
            </div>

            <div className="info">
              <p>Horário: {sunsetData.time}</p>
              <p>Direção: {sunsetData.azimuth.toFixed(1)}°</p>
              <p className="help">Gire o celular para alinhar a bússola</p>
            </div>
          </div>
        )}
      </header>
    </div>
  );
}

export default App;
